import React from 'react'
import { StyledInput, InputDiv, TitleDiv, TextBoxDiv, InputTitle } from '../styles'

export const Input = ({
  label, type, state, setState, onKeyPress, setSucc
}) => (
  <InputDiv>
    <TitleDiv>
      <InputTitle>{label}</InputTitle>
    </TitleDiv>
    <TextBoxDiv>
      <StyledInput
        value={state}
        type={type}
        onChange={e => {
          setState(e.target.value)
          !!setSucc && setSucc("")
        }}
        onKeyPress={onKeyPress}
      />
    </TextBoxDiv>
  </InputDiv>
)

export default Input