import React from 'react'
import { numPuzzles, getDate } from '../../../App'
import { StyledSelect, InputDiv, TitleDiv, TextBoxDiv, InputTitle } from '../styles'

export const Dropdown = ({
  label, state, setState, options, setSucc
}) => (
  <InputDiv>
    <TitleDiv>
      <InputTitle>{label}</InputTitle>
    </TitleDiv>
    <TextBoxDiv>
      <StyledSelect value={state} onChange={e => {
        setState(e.target.value)
        setSucc("")
      }} >
        {options.map((el,idx) => (
          <option key={idx} value={el}>{el <= numPuzzles ? el : getDate()+(getDate+1).substring(46,49)}</option>
        ))}
      </StyledSelect>
    </TextBoxDiv>
  </InputDiv>
)

export default Dropdown