import React, { useState, useEffect } from 'react'
import Scores from './components/Scores'
import {
  Title,
  BackgroundDiv,
  BodyDiv,
} from '../styles'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { decodeToken } from '../auth'

const Leaderboard = () => {
  const [user, setUser] = useState({})
  const [loggedId, setId] = useState(decodeToken(localStorage.getItem('token')).id)
  const [loading, setLoading] = useState(!!localStorage.getItem('token'))
  useEffect(() => {
    document.title = "Leaderboard | Harvard Puzzle Hunt"
  }, [])
  const logout = () => {
    setId(null)
    setLoading(false)
  }
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} customLogout={logout} loading={loading} onPage="Leaderboard" />
      <BodyDiv>
        {/* <StyledLink to="/">Home</StyledLink>
        <StyledLink to="/submit">Submit</StyledLink>
        <StyledLink to="/puzzles">Puzzles</StyledLink> */}
        <Title>Leaderboard: Summer 2021 Contest</Title>
        <Scores setUser={setUser} loggedId={loggedId} setLoading={setLoading} />
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default Leaderboard
