import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { USER_BY_ID } from '../graphql'
import {
  BackgroundDiv, BodyDiv, Title, Subtitle, StyledP, Subsubtitle, SubP, HiddenLink,
} from '../styles'
import { PuzzleLink } from './styles'
import { getDate } from '../../App'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { decodeToken } from '../auth'

const Secret = () => {
  const [user, setUser] = useState({})
  const date = getDate()+(getDate+1).substring(46,49)
  useEffect(() => {
    document.title = `${date.charAt(0).toUpperCase()+date.slice(1)+" Puzzle"} | Harvard Puzzle Hunt`
  }, [])
  const { loading } = useQuery(USER_BY_ID, {
    variables: {
      id: decodeToken(localStorage.getItem('token')).id
    },
    onCompleted: ({ userById: user }) => {
      setUser(user)
    },
    skip: !localStorage.getItem('token'),
  })
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} loading={loading} />
      <BodyDiv home={true} >
        <Title>You found the secret puzzle!</Title>
        <StyledP>
          However, before you can access the secret puzzle, you'll need to crack the password...
          maybe consider joining our <b>mailing list</b> (again?) for some inspiration... Use the password
          to <b>access the puzzle itself</b>, and <b>enter the password on the submissions
          page</b> (under any puzzle) in order to be able to <b>unlock the submission portal</b> for it. Good luck!
        </StyledP>
        <PuzzleLink to={`/puzzles/${date}`}>Continue to puzzle...</PuzzleLink>
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default Secret
