import React, { useEffect } from 'react'
import { useParams, Navigate } from 'react-router-dom'
import titles from './titles'
import { FullDiv } from './styles'
import { getDate } from '../../App'

const Puzzle = () => {
  const date = getDate()+(getDate+1).substring(46,49)
  const { puzzle } = useParams()
  if (!"1,2,3,4".split(",").includes(puzzle) && puzzle != "secret") {
    return <Navigate to="/puzzles" replace/>
  }
  const title = titles[puzzle-1]
  useEffect(() => {
    document.title = `${!!title ? title : date.charAt(0).toUpperCase()+date.slice(1)+" Puzzle"} | Harvard Puzzle Hunt`
  }, [])
  return (
    <FullDiv>
      <object data={`${title}.pdf`} type="application/pdf" width="100%" height="100%" />
    </FullDiv>
  )
}

export default Puzzle
