import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { USER_BY_ID } from '../graphql'
import {
  BackgroundDiv,
  Title,
  BodyDiv,
} from '../styles'
import {
  PuzzleLink,
} from './styles'
import { numPuzzles } from '../../App'
import titles from '../Puzzle/titles'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { decodeToken } from '../auth'

const PuzzleHome = () => {
  const [user, setUser] = useState({})
  useEffect(() => {
    document.title = "Puzzles | Harvard Puzzle Hunt"
  }, [])
  const { loading } = useQuery(USER_BY_ID, {
    variables: {
      id: decodeToken(localStorage.getItem('token')).id
    },
    onCompleted: ({ userById: user }) => {
      setUser(user)
    },
    skip: !localStorage.getItem('token'),
  })
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} loading={loading} onPage="Puzzles" />
      <BodyDiv>
        <Title>Puzzles: Summer 2021 Contest</Title>
        {/* <StyledLink to="/">Home</StyledLink>
        <StyledLink to="/leaderboard">Leaderboard</StyledLink>
        <StyledLink to="/submit">Submit</StyledLink> */}
        {[...Array(numPuzzles+1).keys()].slice(1).map((el,idx) => <PuzzleLink key={idx} to={`/puzzles/${el}`}>{titles[el-1]}</PuzzleLink>)}
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default PuzzleHome
