import React from 'react'
import PuzzTdBackground from './PuzzTdBackground'
import {
  RankTd,
  NameTd,
  TimeTd,
  StyledRow,
} from '../styles'
import { numPuzzles, contestOver } from '../../../App'

const Entry = ({
  index, displayName, scores, updatedAt, realUpdatedAt, score, firsts, lastTwo, show, id, loggedId, foundSecret,
}) => {
  const numPuzzlesPlus1 = numPuzzles + 1
  const highlight = id == loggedId
  // const highlight = index == loggedIn
  const getDateString = milli => {
    const date = new Date(parseInt(milli))
    const dateStr = date.toLocaleDateString("en-US", {
      timeZone:'America/New_York',
      day:'numeric',
      month:'numeric',
      year:'2-digit',
    })
    const timeStr = date.toLocaleTimeString("en-US", {
      timeZone:'America/New_York',
      hour:'numeric',
      minute:'numeric',
    })
    const l = timeStr.length
    const ampm = timeStr[l-2] == "A" ? "am" : "pm"
    const time = timeStr.substring(0,l-3)
    return dateStr+' '+time+ampm+(!contestOver && score - (!scores[numPuzzlesPlus1] ? 0 : 1) == numPuzzlesPlus1 - 2 && !highlight ? ' (?)' : '')
  }
  // const newScores = scores.concat(Array(numPuzzles - scores.length + 1).fill(0))
  const newScores = !scores[numPuzzlesPlus1]
    ? scores.slice(0,numPuzzles).concat(Array(numPuzzles - scores.length + 1).fill(0))
    : scores.slice(0,numPuzzles).concat(scores.slice(numPuzzlesPlus1))
  // if (highlight) {
  //   newScores.forEach((el,idx) => {
  //     console.log(lastTwo[idx] && !contestOver && highlight && el)
  //   })
  // }
  const getType = (el,idx) => ({
    done: el > 0,
    first: !!firsts[idx],
    lastTwo: !contestOver && !highlight && !!lastTwo[idx],
    cantSee: !contestOver && el == 1,
  })
  return (
    <StyledRow highlight={highlight}>
      <RankTd>{`${index + 1}`}</RankTd>
      <NameTd>{`${displayName}`}</NameTd>
      <TimeTd>{`${getDateString(highlight ? realUpdatedAt : updatedAt)}`}</TimeTd>
      {/* {newScores.slice(0,numPuzzles).map((el,idx) => {
        return (<PuzzTd key={idx} {...getType(el,idx)} title="hi"/>)
      })}
      {!!show && (newScores[numPuzzles] > 0 || highlight || contestOver) && <PuzzTd {...getType(newScores[numPuzzles],numPuzzles)} />} */}
      {newScores.slice(0,numPuzzles).map((el,idx) => (
        <PuzzTdBackground key={idx} type={getType(el,idx)} />
      ))}
      {!!show && (foundSecret || newScores[numPuzzles] > 0 || contestOver) &&
        <PuzzTdBackground type={getType(newScores[numPuzzles],numPuzzlesPlus1)} />
      }
    </StyledRow>
  )
}

export default Entry