import React, { useState, useEffect } from 'react'
import {
  Title, BackgroundDiv, BodyDiv,
} from '../styles'
import Form from './components/Form'
import Header from '../components/Header'
import Footer from '../components/Footer'

const Submit = () => {
  const [user, setUser] = useState({})
  const [err, setErr] = useState("")
  const [token, setToken] = useState(localStorage.getItem('token'))
  const [tokenExists, setTokenExists] = useState(!!localStorage.getItem('token'))
  const [loading, setLoading] = useState(!!localStorage.getItem('token'))
  useEffect(() => {
    document.title = "Submit | Harvard Puzzle Hunt"
  }, [])
  const logout = () => {
    setTokenExists(false)
    setLoading(false)
    setToken("")
    setErr("")
  }
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} customLogout={logout} loading={loading} onPage="Submit" />
      <BodyDiv>
        {/* <StyledLink to="/">Home</StyledLink>
        <StyledLink to="/leaderboard">Leaderboard</StyledLink>
        <StyledLink to="/puzzles">Puzzles</StyledLink> */}
        <Title>Submission Page: Summer 2021 Contest</Title>
        <Form
          user={user} setUser={setUser}
          err={err} setErr={setErr}
          token={token} setToken={setToken}
          tokenExists={tokenExists} setTokenExists={setTokenExists}
          setLoading={setLoading}
          logout={logout}
        />
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default Submit
