import React, { useState } from 'react'
import { useQuery } from '@apollo/react-hooks'
import { USER_BY_ID } from '../graphql'
import {
  BackgroundDiv, BodyDiv, Title, Subtitle, StyledP, Subsubtitle, SubP, HiddenLink,
} from '../styles'
import { SponsorA, SponsorImage } from './styles'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { decodeToken } from '../auth'
import { getDate } from '../../App'
import { contestStarted } from '../../App'

const Home = () => {
  const [user, setUser] = useState({})
  const { loading } = useQuery(USER_BY_ID, {
    variables: {
      id: decodeToken(localStorage.getItem('token')).id
    },
    onCompleted: ({ userById: user }) => {
      setUser(user)
    },
    skip: !localStorage.getItem('token'),
  })
  const date = getDate()+(getDate+1).substring(46,49)
  return (
    <BackgroundDiv>
      <Header user={user} setUser={setUser} loading={loading} onPage="Home" />
      <BodyDiv home={true}>
        <Title home={true}>Welcome to Harvard Puzzle Club's Fall 2022 Puzzle Hunt!</Title>
        <Subtitle>About Us</Subtitle>
        <StyledP>
          We've set out to start a club at Harvard centered entirely around <b>recreational (and
          sometimes slightly competitive) puzzle solving</b>. A couple of us actually met through
          CS50 Puzzle Day, and we really loved how that event pulled together a huge community
          of puzzle-solvers... but sadly it only happens once a year! Harvard Undergraduate
          Puzzle Club aims to change that by being active year-round for all things puzzle-y,
          hopefully including a few hunts per year. We'll have regular puzzle sessions for
          sharing puzzles we've written and challenging our friends to try to solve them. Some
          of them could end up in an actual hunt! If you like puzzles at all, please <b>join our
          mailing list below</b>!
        </StyledP>
        <Subtitle>About the Contest</Subtitle>
        <StyledP>
          Last summer, we held the first run of the Harvard Puzzle Hunt. Now, this fall hunt
          is just our first hunt as an official club, so any feedback about bugs, design suggestions,
          or anything in general is much appreciated (links below).
        </StyledP>
        <Subsubtitle>Registering</Subsubtitle>
        <SubP>
          You can register as an <b>individual or as a team of up to 8 people</b>, but keep in mind
          you'll have to share the prize among your whole team. To simplify things, the website is
          configured such that registering a new team and logging in as a returning team use the
          same form, located under the <b>Submit page</b>. If you've ever used when2meet, the form works
          the same way. To register, simply enter your information, and make sure the rest of your
          team also knows the credentials. Then, when you return, you can enter the same information
          to log in. If you want us to be able to contact you should you win a prize, make sure
          to <b>fill in your email</b>! <b>Do not abuse the submission form</b>. We expect teams to
          uphold the spirit of the hunt and any malicious behavior will be penalized.
        </SubP>
        <Subsubtitle>Puzzles</Subsubtitle>
        <SubP>
          <b>Puzzles can be solved in any order</b>, but some are harder than others. The length of the
          contest is currently set to be 1 month, but we may extend the time frame depending on
          how teams are progressing. <b>The winner will be the team who solves the most puzzles, and,
          to break ties, in the shortest amount of time.</b> Since team sizes are large and you have
          a month to solve these, one or more of the puzzles may take a pretty long time. We highly
          suggest splitting up grunt work among multiple team members if you're willing to split
          the prize. The leaderboard keeps track of teams' puzzle-specific progress so feel free to use
          this information to help your team decide which puzzles to work on. It also keeps track of
          which teams solve each puzzle first and there's a
          prize for the team with the most first solves (excluding other prize winners)-- so get
          started early! The leaderboard will hide your last few solves in order to maintain some
          level of mystery throughout the hunt, and final standings won't be released until the
          hunt is over. Be on the lookout for extra clues-- <b>there might be some {
            contestStarted ? <HiddenLink to={`/${date}`}>hidden features...</HiddenLink> : "hidden features..."
          } </b>
        </SubP>
        <Subsubtitle>Prizes</Subsubtitle>
        <SubP>
          A huge thank you to <b>El Jefe's Taqueria</b> for sponsoring the prizes for this hunt!
        </SubP>
        <SponsorA href="https://www.eljefestaqueria.com/" target="_blank">
          <SponsorImage src="https://static.wixstatic.com/media/297086_ef66ce8f869d42c6a47263ee96e2aae5.png/v1/fit/w_2500,h_1330,al_c/297086_ef66ce8f869d42c6a47263ee96e2aae5.png"/>
        </SponsorA>
        <SubP>
          The overall <b>first place team will win the grand prize of $50 in giftcards to El
          Jefe's Taqueria</b>, and the <b>second place team will win a $25 giftcard to
          Jefe's</b>. A third <b>$25 giftcard to Jefe's will be awarded to the team with the most
          first solves</b> (not including first and second place). Ties will be broken
          based on the maximum time across each of your firsts for a second team to solve it after you
          did (if at all). Prizes can get even
          bigger once we become a non-provisional club-- join our mailing list to help make that
          happen! If you want to redeem a prize, make sure you've filled out your email in the
          form so that we know who you are and have a way of contacting you.
        </SubP>
        <Subtitle>Interested in Joining?</Subtitle>
        <StyledP>
          If you enjoy the hunt and want to participate next time, please <b>join our mailing list
          below</b> so that you're up to date on all Harvard Puzzle Club news. If you want to be a
          part of the next contest <b>as a puzzle-<i>writer</i></b>, there's a place to indicate that on the
          form as well.
        </StyledP>
        <Title bottom={true}>Good Luck Puzzlers!</Title>
      </BodyDiv>
      <Footer/>
    </BackgroundDiv>
  )
}

export default Home
