import gql from 'graphql-tag'

export const ALL_USERS = gql`
  query allUsers {
    allUsers {
      id
      displayName
      score
      firsts
      lastTwo
      foundSecret
      updatedAt
      realUpdatedAt
    }
  }
`

export const USER_BY_ID = gql`
  query userById($id: ID!) {
    userById(id: $id) {
      id
      displayName
      email
      score
      firsts
      foundSecret
      createdAt
      realUpdatedAt
    }
  }
`

export const LOGIN = gql`
  mutation login($displayName: String!, $password: String!) {
    login(displayName: $displayName, password: $password) {
      token
      user {
        id
        displayName
        email
        score
        firsts
        foundSecret
        createdAt
        realUpdatedAt
      }
    }
  }
`

export const UPDATE_EMAIL = gql`
  mutation updateEmail($id: ID!, $email: String!) {
    updateEmail(id: $id, email: $email) {
        id
        displayName
        email
        score
        firsts
        createdAt
        realUpdatedAt
    }
  }
`

export const UPDATE_PUZZLES = gql`
  mutation updatePuzzles($id: ID!, $puzzle: Int!, $answer: String!) {
    updatePuzzles(id: $id, puzzle: $puzzle, answer: $answer) {
        id
        displayName
        email
        score
        firsts
        foundSecret
        createdAt
        realUpdatedAt
    }
  }
`
