import React, { useState } from 'react'
import { useQuery, useMutation } from '@apollo/react-hooks'
import Input from './Input'
import Dropdown from './Dropdown'
import { numPuzzles, contestOver } from '../../../App'
import { ALL_USERS, USER_BY_ID, LOGIN, UPDATE_EMAIL, UPDATE_PUZZLES } from '../../graphql'
import {
  FancyButton, FormDiv,
  MessageDiv, ErrorMsg, LoadingMsg, SuccessMsg,
} from '../styles'
import { decodeToken } from '../../auth'

const Form = ({ user, setUser, err, setErr, token, setToken, tokenExists, setTokenExists, setLoading }) => {
  const [name, setName] = useState('')
  const [password, setPass] = useState('')
  const [emailErr, setEmailErr] = useState("")
  const [succ, setSucc] = useState("")
  const [emailSucc, setEmailSucc] = useState(false)
  const [email, setEmail] = useState("")
  const [puzzle, setPuzzle] = useState(1)
  const [answer, setAnswer] = useState("")
  const [options, setOptions] = useState([...Array(numPuzzles+1).keys()].slice(1))
  const checkErr = (err, msg) => {
    setLoading(false)
    if (err.includes("CUSTOM:")) {
      setErr(err.replace('GraphQL error: CUSTOM: ', ''))
    } else {
      setErr(msg)
    }
  }
  const lowestUnsolved = user => {
    const scores = user.score.toString(3).split("").reverse().map(el => parseInt(el))
    const num = numPuzzles+(user.foundSecret ? 1 : 0)
    for (let i = 0; i < num; i++) {
      if (!scores[i]) {
        setPuzzle(i+1)
        return
      }
    }
    setPuzzle(num)
  }
  const { loading: userLoading } = useQuery(USER_BY_ID, {
    variables: {
      id: decodeToken(token).id
    },
    onError: error => {
      setToken("")
      setErr("Previous credentials invalid. Please re-enter your information.")
      setLoading(false)
    },
    onCompleted: ({ userById: user }) => {
      setUser(user)
      setLoading(false)
      setEmail(!!user.email ? user.email : "")
      setErr("")
      setOptions([...Array(numPuzzles+1+(user.foundSecret ? 1 : 0)).keys()].slice(1))
      lowestUnsolved(user)
    },
    skip: !tokenExists,
  })
  const [login, { loading: loginLoading }] = useMutation(LOGIN, {
    variables: {
      displayName: name,
      password,
    },
    onError: error => {
      // checkErr(error.message, "Login failed. Try refreshing the page.")
      checkErr(error.message, error.message)
    },
    onCompleted: ({ login: { user, token }}) => {
      localStorage.setItem('token', token)
      setLoading(false)
      setUser(user)
      setEmail(!!user.email ? user.email : "")
      setErr("")
      setEmailErr("")
      setSucc("")
      setEmailSucc("")
      setOptions([...Array(numPuzzles+1+(user.foundSecret ? 1 : 0)).keys()].slice(1))
      lowestUnsolved(user)
    },
    refetchQueries: [
      { query: ALL_USERS },
    ]
  })
  const [updateEmail, { loading: emailLoading }] = useMutation(UPDATE_EMAIL, {
    variables: {
      id: user.id,
      email,
    },
    onError: error => {
      checkErr(error.message, "Failed to update email. Try refreshing the page.")
    },
    onCompleted: data => {
      setEmailErr("")
      setEmailSucc(true)
    },
  })
  const upd = () => {
    console.log(user.id,puzzle - 1 == numPuzzles ? numPuzzles + 1 : puzzle - 1,answer)
    updatePuzzles()
  }
  const contestOverUpd = () => {
    setErr("")
    setTimeout(() => setErr("Contest over! Portal closed!"), 80)
  }
  const [updatePuzzles, { loading: puzzLoading }] = useMutation(UPDATE_PUZZLES, {
    variables: {
      id: user.id,
      // puzzle: puzzle - 1,
      puzzle: puzzle - 1 == numPuzzles ? numPuzzles + 1 : puzzle - 1,
      answer,
    },
    onError: error => {
      // checkErr(error.message, "Failed to process submission. Try refreshing the page.")
      checkErr(error.message, error.message)
    },
    onCompleted: ({ updatePuzzles: user }) => {
      setErr("")
      setOptions([...Array(numPuzzles+1+(user.foundSecret ? 1 : 0)).keys()].slice(1))
      if (!user.displayName) {
        setPuzzle(numPuzzles+1)
        setSucc("You found the secret puzzle!")
      } else {
        const indx = puzzle - 1 == numPuzzles ? numPuzzles + 1 : puzzle - 1
        const score = user.score.toString(3).split("").reverse()[indx]
        const first = user.firsts.toString(2).split("").reverse()[indx]
        console.log(score,first)
        if (score == "1" && !contestOver) {
          setSucc("Correct! And you might be first!")
        } else if (first == "1") {
          setSucc("Correct! And you're the first to solve this puzzle!")
        } else {
          setSucc("Correct!")
        }
      }
    },
    refetchQueries: [
      { query: ALL_USERS },
    ]
  })
  const verifyUsername = () => {
    const l = name.length
    if (l < 2 || l > 20) {
      return false
    }
    if (name[0] === ' ' || name[l-1] === ' ') {
      return false
    }
    return (/^[0-9a-zA-Z`~!@#$%^= +\[\]{}\\|;:'",<>./?&*_()-]*$/.test(name))
  }
  const verifyPassword = () => {
    return password.length >= 3
  }
  const loading = () => (!!token && userLoading) || loginLoading
  const loginButton = () => {
    setLoading(true)
    setTokenExists(false)
    let errStr = ""
    if (!verifyUsername()) {
      errStr += "Username must have 2-20 characters, no leading or trailing spaces, and use only characters on a standard keyboard."
    }
    if (!verifyPassword()) {
      errStr += !!errStr ? " " : ""
      errStr += "Password must have at least 3 characters."
    }
    setErr(errStr)
    if (!errStr) {
      login()
      setAnswer("")
    } else {
      setLoading(false)
    }
  }
  const loginEnter = e => {
    if (e.code === "Enter" || e.code === "NumpadEnter") {
      loginButton()
    }
  }
  const answerEnter = e => {
    if (e.code === "Enter" || e.code === "NumpadEnter") {
      contestOver ? contestOverUpd() : upd()
    }
  }
  const emailButton = () => {
    const emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    const validEmail = emailRegex.test(email.toLowerCase())
    if (!validEmail) {
      setEmailErr('Please enter a valid email.')
      return
    }
    updateEmail()
  }
  const emailEnter = e => {
    if (e.code === "Enter" || e.code === "NumpadEnter") {
      emailButton()
    }
  }
  return (
    <>
      { !user.id ?
        <FormDiv>
          { !token &&
            <>
              <Input
                label="Team Name"
                type="text"
                state={name}
                setState={setName}
                onKeyPress={loginEnter}
              />
              <Input
                label="Password"
                type="password"
                state={password}
                setState={setPass}
                onKeyPress={loginEnter}
              />
              <FancyButton onClick={loginButton}>Sign In</FancyButton>
              {/* <p>{`${name} ${password}`}</p> */}
            </>
          }
          <MessageDiv>
            {loading() ? <LoadingMsg>{
              tokenExists
                ? "Previous credentials detected. Attempting to sign in..."
                : "Signing in..."
            }</LoadingMsg> :
             !!err && <ErrorMsg>{err}</ErrorMsg>}
          </MessageDiv>
        </FormDiv>
      : <FormDiv>
          <Dropdown
            label="Puzzle"
            state={puzzle}
            setState={setPuzzle}
            options={options}
            setSucc={setSucc}
          />
          <Input
            label="Answer"
            type="text"
            state={answer}
            setState={setAnswer}
            onKeyPress={answerEnter}
          />
          <FancyButton onClick={contestOver ? contestOverUpd : upd}>Submit</FancyButton>
          <MessageDiv>
            {puzzLoading ? <LoadingMsg>{`Processing submission...`}</LoadingMsg> :
             !!err ? <ErrorMsg>{err}</ErrorMsg> :
             !!succ && <SuccessMsg>{succ}</SuccessMsg>}
          </MessageDiv>
          {/* <p>{`${user.id} ${puzzle} ${answer}`}</p> */}
          <Input
            label="Email"
            type="text"
            state={email}
            setState={setEmail}
            onKeyPress={emailEnter}
            setSucc={setEmailSucc}
          />
          <FancyButton onClick={emailButton}>Update</FancyButton>
          <MessageDiv>
            {emailLoading ? <LoadingMsg>{`Updating...`}</LoadingMsg> :
             !!emailErr ? <ErrorMsg>{emailErr}</ErrorMsg> :
             !!emailSucc && <SuccessMsg>Updated!</SuccessMsg>}
          </MessageDiv>
        </FormDiv>
      }
    </>
  )
}

export default Form
