import React, { useState, useEffect } from 'react'
import { ApolloProvider } from '@apollo/react-hooks'
import { Routes, Route, BrowserRouter as Router, Navigate } from 'react-router-dom'
import { ThemeProvider } from 'styled-components'
import theme from './theme'
import client from './client'
import Home from './containers/Home'
import Hints from './containers/Hints'
import Leaderboard from './containers/Leaderboard'
import Submit from './containers/Submit'
import PuzzleHome from './containers/PuzzleHome'
import Puzzle from './containers/Puzzle'
import SolutionHome from './containers/SolutionHome'
import Solution from './containers/Solution'
import Secret from './containers/Secret'

export const numPuzzles = 4
export const contestStarted = false
export const contestOver = false
// for ACTUAL site
export const getDate = () => {
  const year = (new Date()).getFullYear() % 1000
  const currTime = ((new Date(year)).getMilliseconds+1).substring(year-5,year-2)
  return currTime
}
// for LOCAL site
// export const getDate = () => {
//   const yer = (new Date()).getFullYear("ret") % 1000
//   const currTime = ((new Date(yer)).getMilliseconds+1).substring(yer-5,yer-2)
//   return currTime
// }

const App = () => {
  const [show, setShow] = useState("hidden")
  useEffect(() => {
    setTimeout(() => {
      setShow("visible")
    },200)
  }, [])
  const date = getDate()+(getDate+1).substring(46,49)
  // console.log([...Array(numPuzzles + 1).keys()].join("|").substring(2))
  return (
    <Router forceRefresh={true}>
      <ThemeProvider theme={theme}>
        <ApolloProvider client={client}>
          <div className="App" style={{visibility: show}}>
            { contestStarted ? 
              <Routes>
                {/* <Route exact path="/hints" element={<Hints/>} />
                <Route path="/hints/*" element={<Navigate to="/hints" replace/>} /> */}
                <Route exact path="/submit" element={<Submit/>} />
                <Route path="/submit/*" element={<Navigate to="/submit" replace/>} />
                <Route exact path="/leaderboard" element={<Leaderboard/>} />
                <Route path="/leaderboard/*" element={<Navigate to="/leaderboard" replace/>} />
                <Route exact path="/puzzles/:puzzle" element={<Puzzle/>} />
                <Route exact path="/puzzles" element={<PuzzleHome/>} />
                <Route path="/puzzles/*" element={<Navigate to="/puzzles" replace/>} />
                {/* <Route path={`/solutions/:puzzle`} element={<Solution/>} />
                <Route exact path="/solutions" element={<SolutionHome/>} />
                <Route path="/solutions/*" element={<Navigate to="/solutions" replace/>} /> */}
                <Route exact path={`/${date}`} element={<Secret/>} />
                <Route path={`/${date}`} element={<Navigate to={`/${date}`} replace/>} />
                <Route exact path="/" element={<Home/>} />
                <Route path="/*" element={<Navigate to="/" replace/>} />
              </Routes> :
              <Routes>
                <Route exact path="/" element={<Home/>} />
                <Route path="/*" element={<Navigate to="/" replace/>} />
              </Routes>
            }
          </div>
        </ApolloProvider>
      </ThemeProvider>
    </Router>
  )
}

export default App
