import React, { useState } from 'react'
import { HeaderLink } from './styles'

const NavLink = ({ link, currOnPage, wantOnPage, cancel, setCancel }) => (
  <HeaderLink
    to={link}
    highlight={currOnPage==wantOnPage ? "yes" : ""}
    cancel={!!cancel ? "yes" : ""}
    onClick={() => setCancel(true)}
  >{wantOnPage}</HeaderLink>
)

export default NavLink
