import React from 'react'
import { PuzzTd } from '../styles'

const PuzzTdBackground = ({ type }) => (
  (type.done || type.lastTwo) ?
  <PuzzTd
    img={
      type.lastTwo ? "url('https://harvardpuzzles.github.io/leaderboard/questionstraight.png')" :
      type.cantSee ? "url('https://harvardpuzzles.github.io/leaderboard/check_first%20checkstraight.png')" :
      type.first ? "url('https://harvardpuzzles.github.io/leaderboard/first%20check.png')" :
      type.done && "url('https://harvardpuzzles.github.io/leaderboard/check.png')"
    }
    title={
      type.lastTwo ? "Maybe solved?" :
      type.cantSee ? "Solved! Maybe first?" :
      type.first ? "First to solve!" :
      type.done && "Solved!"
    }
    lastTwo={type.lastTwo}
  /> :
  <PuzzTd/>
)

export default PuzzTdBackground