import React, { useState } from 'react'
import {
  StyledHeader, HeaderLeft, HeaderRight,
  HeaderButton, HeaderText, Loader, HeaderNotLink,
} from './styles'
import NavLink from './NavLink'
import { contestStarted } from '../../../App'

const Header = ({ user, setUser, customLogout, loading, onPage }) => {
  const [cancel, setCancel] = useState(false)
  const logout = () => {
    localStorage.removeItem('token')
    setUser({})
    if (!!customLogout) {
      customLogout()
    }
  }
  if (contestStarted) {
    return (
      <StyledHeader>
        <HeaderLeft>
          <NavLink link="/" currOnPage={onPage} wantOnPage="Home" cancel={cancel} setCancel={setCancel}>Home</NavLink>
          <NavLink link="/leaderboard" currOnPage={onPage} wantOnPage="Leaderboard" cancel={cancel} setCancel={setCancel}>Leaderboard</NavLink>
          <NavLink link="/submit" currOnPage={onPage} wantOnPage="Submit" cancel={cancel} setCancel={setCancel}>Submit</NavLink>
          <NavLink link="/puzzles" currOnPage={onPage} wantOnPage="Puzzles" cancel={cancel} setCancel={setCancel}>Puzzles</NavLink>
          {/* <NavLink link="/hints" currOnPage={onPage} wantOnPage="Hints" cancel={cancel} setCancel={setCancel}>Hints</NavLink> */}
          {/* <NavLink link="/solutions" currOnPage={onPage} wantOnPage="Solutions" cancel={cancel} setCancel={setCancel}>Solutions</NavLink> */}
        </HeaderLeft>
        { (!!user && !!user.displayName) ?
          <HeaderRight>
            <HeaderText>{`Welcome, ${user.displayName}!`}</HeaderText>
            <HeaderButton onClick={logout}>Logout</HeaderButton>
          </HeaderRight> :
          (loading && <Loader/>)
        }
      </StyledHeader>
    )
  }
  return (
    <StyledHeader>
      <HeaderLeft>
        <NavLink link="/" currOnPage={onPage} wantOnPage="Home" cancel={cancel} setCancel={setCancel}>Home</NavLink>
        <HeaderNotLink title="locked">Leaderboard</HeaderNotLink>
        <HeaderNotLink title="locked">Submit</HeaderNotLink>
        <HeaderNotLink title="locked">Puzzles</HeaderNotLink>
        {/* <HeaderNotLink title="locked">Hints</HeaderNotLink> */}
        {/* <HeaderNotLink title="locked">Solutions</HeaderNotLink> */}
      </HeaderLeft>
      <HeaderRight>
        <HeaderText>Hunt will open soon!</HeaderText>
      </HeaderRight>
    </StyledHeader>
  )
}

export default Header
