import React, { useState } from 'react'
import { useQuery } from '@apollo/react-hooks'
import Entry from './Entry'
import { numPuzzles, contestOver } from '../../../App'
import titles from '../../Puzzle/titles'
import {
  StyledTable,
  RankTd,
  NameTd,
  PuzzTd, PuzzLink, PuzzTitle,
  TimeTd,
  StyledTableBody,
  StyledTableHead,
} from '../styles'
import { ALL_USERS } from '../../graphql'
import { decodeToken } from '../../auth'

const Scores = ({ setUser, loggedId, setLoading }) => {
  const numPuzzlesPlus1 = numPuzzles + 1
  const [hover, setHover] = useState(-2)
  const [show, setShow] = useState(1)
  const { data, error: queryError, loading: queryLoading } = useQuery(ALL_USERS, {
    partialRefetch: true,
    onError: error => { return },
    onCompleted: ({ allUsers }) => {
      setLoading(false)
      // setShow(allUsers.reduce((acc,el) => (
      //   acc+(el.score.toString(3).split("").reverse()[numPuzzles] == "2" ? 1 : 0)
      // ),0) > 0 ? 2 : 1)
      setShow(allUsers.reduce((acc,el) => (
        acc+(el.foundSecret ? 1 : 0)
      ),0) > 0 ? 2 : 1)
    },
  })
  const sortedData = () => {
    let lst = data.allUsers
    const loggedIn = lst.reduce((acc,el,idx) => (
      el.id == decodeToken(localStorage.getItem('token')).id ? idx : acc
    ), -1)
    const scoresCanSee = Array(numPuzzlesPlus1+1).fill(false)
    if (loggedIn != -1) {
      setUser(data.allUsers[loggedIn])
      const scores = lst[loggedIn].score.toString(3).split("").reverse().map(el => parseInt(el))
      const firsts = lst[loggedIn].firsts.toString(2).split("").reverse().map(el => parseInt(el))
      const lastTwo = lst[loggedIn].lastTwo.toString(2).split("").reverse().map(el => parseInt(el))
      lastTwo.forEach((el,idx) => {
        if (!!el) {
          const onlyOne = lst.reduce((acc,el,id) => {
            if (id == loggedIn) {
              return acc
            }
            return (acc && !el.lastTwo.toString(2).split("").reverse().map(el => parseInt(el))[idx])
          },true)
          if (onlyOne){
            scoresCanSee[idx] = true
          }
        }
      })
      scores.forEach((el,idx) => {
        if (el == 2 && firsts[idx] == 1) {
          scoresCanSee[idx] = true
        }
      })
    }
    lst = lst.map((el,idx) => {
      const scores = el.score.toString(3).split("").reverse().map((el,idx) => (
        scoresCanSee[idx] ? (!parseInt(el) ? 0 : 2) : parseInt(el)
      ))
      const firsts = el.firsts.toString(2).split("").reverse().map(el => parseInt(el))
      const lastTwo = el.lastTwo.toString(2).split("").reverse().map(el => parseInt(el))
      const out = {
        ...el,
        scores,
        firsts,
        lastTwo,
        score: scores.reduce((acc,el) => acc+(el > 0 ? 1 : 0), 0),
        updatedAt: el.realUpdatedAt,
        foundSecret: el.foundSecret,
        highlight: idx == loggedIn,
      }
      if (!contestOver) {
        out.updatedAt = el.updatedAt
        out.score -= scores.reduce((acc,el,idx) => acc+(el > 0 ? 1 : 0)*(!!lastTwo[idx] ? 1 : 0), 0)
      }
      return out
    })
    lst.sort((a,b) => (b.score - a.score)*10000000000 + (a.updatedAt - b.updatedAt)*0.0001)
    return lst
  }
  return (
    <>
      <StyledTable>
        <StyledTableHead>
          <tr>
            <RankTd>RK</RankTd>
            <NameTd>NAME</NameTd>
            <TimeTd>LAST SOLVE (ET)</TimeTd>
            {[...Array(numPuzzles+show).keys()].slice(1).map((el,idx) =>
              <PuzzTd key={idx} >
                {idx == numPuzzles ? "?" : (<>
                  <PuzzLink
                    to={`/puzzles/${el}`}
                    onMouseEnter={e => setHover(el)}
                    onMouseLeave={e => setHover(-1)}
                  >{el}</PuzzLink>
                  <PuzzTitle className="puzzTitle" vis={hover==el}>{titles[el-1]}</PuzzTitle>
                </>)}
              </PuzzTd>)
            }
          </tr>
        </StyledTableHead>
        <StyledTableBody>
        {queryLoading || queryError || (
          sortedData().map((entry,idx) => (
            entry ? (
              <Entry
                key={entry.id}
                index={idx}
                {...entry}
                show={show-1}
                loggedId={loggedId}
              />
            ) : ''
          )))}
        </StyledTableBody>
      </StyledTable>
      {queryLoading && <p>Fetching data...</p>}
      {queryError && <p>Failed to fetch data, try refreshing the page.</p>}
    </>
  )
}

export default Scores